:root {
  --shadow: #43a047;
  --scrollbarBG: #ff0055;
  --thumbBG: #66bb6a;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--scrollbarBG);
}

::-webkit-scrollbar-thumb {
  background-color: var(--thumbBG);
  box-shadow: 0 -100vh 0 100vh var(--shadow), 0 0 15px 5px black;
}

@media (max-width: 912px) {
  .App-header {
    flex-direction: column;
  }

  .image-container {
    float: none;
    margin: 0 auto;
  }

  .image-container img {
    width: 280px;
    padding: 10px;
  }

  .text-container {
    text-align: center;
    margin: 20px auto;
  }

  .App-menu {
    display: none;
  }

  /*.col-1-3 {
    width: 50%;
  }
  */
}

@media (min-width: 912px) {
  .blog-card {
    width: 312px;
  }
}
